import { useEffect } from "react"
import { useNavigate } from "react-router-dom"
import { useQueryParams } from "@rp/react/web/common/hooks"
import { useSnackbar } from "./Snackbar"

const useShowQueryParamMessage = (queryParam: string) => {
  const params = useQueryParams()
  const { enqueueSuccess } = useSnackbar()
  const navigate = useNavigate()

  useEffect(() => {
    if (params.get(queryParam)) {
      enqueueSuccess(decodeURIComponent(params.get(queryParam) ?? ""))
      params.delete(queryParam)
      navigate({ search: params.toString() }, { replace: true })
    }
  }, [enqueueSuccess, navigate, params, queryParam])
}

const SUCCESS_MESSAGE_QUERY_PARAM = "successMessage"

export { useShowQueryParamMessage, SUCCESS_MESSAGE_QUERY_PARAM }
